.post {
	width: 100%;
	margin-top: 15px;
	border-radius: 15px;
	background-color: white;
	box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}

.post__image > img {
	width: 100%;
}

.post__top {
	display: flex;
	position: relative;
	align-items: center;
	padding: 15px;
}

.post__avatar {
	margin-right: 10px;
}

.post__topInfo > h3 {
	font-size: medium;
}

.post__topInfo > p {
	font-size: small;
	color: gray;
}

.post__bottom {
	margin-top: 10px;
	margin-bottom: 10px;
	padding: 15px 25px;
}

.post__options {
	padding-top: 10px;
	border-top: 1px solid lightgray;
	border-bottom: 1px solid lightgray;
	display: flex;
	justify-content: space-evenly;
	font-size: medium;
	color: gray;
	cursor: pointer;
	padding: 15px;
}

.post__option {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 5px;
	flex: 1;
}

.post__option > p {
	margin-left: 10px; 
}

.post__option:hover {
	background-color: #eff2f5;
	border-radius: 10px;
}

.post__option--active > p {
	color: #2e81f4;
}

.post__option--active > .MuiSvgIcon-root {
	color: #2e81f4;
}

.post__options > form {
	flex: 1;
	display: flex;
}

.post__options > form > input {
	outline-width: 0;
	border: none;
	padding: 5px 20px;
	margin: 0 10px;
	width: 100%;
	border-radius: 99px;
	background-color: #eff2f5;
}

.post__options > form > button {
	display: none;
}
